import React, {Component} from 'react';
import {Row, Col, Carousel, Form, Input} from 'antd'
import { formItemLayout} from "../../../utils/misc/formLayout";
import LOGO from '../../../assets/images/logo_front.svg'
import {Link, withRouter} from "react-router-dom";
import {connect} from "react-redux";
import {Helmet} from "react-helmet";
import actions from "../../../redux/auth/actions";


@withRouter
@connect(({ auth }) => ({
  auth,
  // menuTopData: menu.menuTopData,
}))
class Index extends Component {

  state = {
    noHp: ''
  }

  handleSubmit = (e) => {
    const { dispatch} = this.props;
    const {noHp} = this.state;
    dispatch({
      type: actions.AUTH_REGISTER,
      payload : {
        phone: +noHp,
        email: noHp,
        name: noHp
      }

    })

    // const {  history} = this.props
    // history.push('/auth/verification')
  }

  render() {

    const { noHp} = this.state
    const contentStyle = {
      height: '80vh',
      color: '#fff',
      lineHeight: '160px',
      textAlign: 'center',
      // background: '#364d79',
    };



    return (
      <div>
        <Helmet titleTemplate="Register" title="resto" />
        <Row className='h-screen'>
          <Col span={16} xs={0} md={16}>
            <Carousel autoplay>
              <div>
                <img style={contentStyle} src='https://cdn123.blob.core.windows.net/image/scap_1.svg' alt='test' />
              </div>
                <div>
                  <img style={contentStyle} src='https://cdn123.blob.core.windows.net/image/scapImage_2.png' alt='test' />
                </div>
            </Carousel>
          </Col>
          <Col span={8} className='shadow-lg' xs={24} md={8}>
            <div className='m-8'>

              {/*TODO LOGO */}

              <div className='text-center' style={{height: '30vh'}}>
                <img src={LOGO} alt='test' className='w-1/3'/>
              </div>

              {/*TODO FORM LOGIN */}
              <Form layout='vertical' onFinish={this.handleSubmit}>
                <Form.Item
                  {...formItemLayout}
                  label={(<span className='text-center'>Masukan Hp Kamu</span>)}
                  name='telp'
                  initialValue={noHp}
                  rules={[{required: false, message:'Mohon Isi Email', pattern: /^[0-9\b]+$/}]}
                >
                  <Input
                    className ='font-extrabold text-4xl text-center'
                    maxLength={13}
                    value={noHp}
                    size='large'
                    type='tel'
                    bordered={false}
                    autoFocus
                    placeholder='08566566776'
                    onChange={e => this.setState({noHp: e.target.value})}
                  />
                  <p className='text-center text-red-600'>{noHp.length <11 && noHp.length !== 0 ? <span className='text-center text-red-600'>Format No Hp Salah</span>: null}</p>
                </Form.Item>
                <Form.Item>

                  <button
                    disabled={noHp.length < 12}
                    htmltype='submit'
                    className={` ${noHp.length < 11 ? "cursor-not-allowed" : "cursor-pointer" } mt-8 mb-4 w-full bordered  ${noHp.length > 11 ? 'bg-blue-500' :'bg-grey-500'} hover:${noHp.length > 11 ? 'bg-blue-400' :'bg-grey-500'} ${noHp.length <= 11 ? 'text-grey-800' :'text-white'} font-bold py-2 px-4 rounded`}>
                    REGISTER
                  </button>
                  Sudah Memiliki Akun? <Link to="/auth/login">Masuk</Link>
                </Form.Item>
                <p>Kami akan menjaga kerahasiaan informasi dan data pribadimu.</p>

              </Form>
            </div>

          </Col>
        </Row>
      </div>

  );
  }
}

export default Index;
